
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'


export default {
components: {
Group,
Cell,
Confirm
},
data () {
return {
avartar: 'https://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83eoLKnzzlSdMqQzIPhdxIZ2JyROWeajaMMib8Lq8PfmDnic3ou9oU7jJ8U21Esu4OQibRr04ccYy8THXA/132',
msg: 'Hello World!',
clientHeight:''
}
},
mounted(){
const that =this;

this.clientHeight =   `${document.documentElement.clientHeight}`          
